@charset "UTF-8";
#data-content .myHeader-data {
  /*关闭状态*/
  /*开通状态*/
  /*    特征值选择区域*/
}
#data-content .myHeader-data .dashDiv {
    position: absolute;
    right: 150px;
    top: 25px;
    border-radius: 20px;
    border: 1px solid #CBCBCB;
    width: 75px;
    height: 30px;
    line-height: 30px;
    box-sizing: border-box;
    background: #CBCBCB;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
}
#data-content .myHeader-data .dashDiv .dashSpan {
    border-radius: 50%;
    border: 1px solid #CBCBCB;
    background-color: #FFFFFF;
    display: inline-block;
    width: 28px;
    height: 28px;
    box-sizing: border-box;
}
#data-content .myHeader-data .dashDiv .dashText {
    display: inline-block;
    padding-right: 6px;
    height: 30px;
    line-height: 28px;
}
#data-content .myHeader-data .dashDivSelectd {
    position: absolute;
    right: 150px;
    top: 25px;
    border-radius: 20px;
    border: 1px solid #436EF6;
    width: 75px;
    height: 30px;
    line-height: 30px;
    box-sizing: border-box;
    background: #436EF6;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    transition: .5s;
    flex-flow: row-reverse;
}
#data-content .myHeader-data .dashDivSelectd .dashSpan {
    border-radius: 50%;
    border: 1px solid #fff;
    background-color: #FFFFFF;
    width: 28px;
    display: inline-block;
    height: 28px;
    box-sizing: border-box;
}
#data-content .myHeader-data .dashDivSelectd .dashText {
    display: inline-block;
    padding-left: 6px;
    height: 30px;
    line-height: 28px;
    color: #fff;
}
#data-content .myHeader-data .headerBtn {
    position: absolute;
    right: 20px;
    top: 25px;
    width: 100px;
    height: 30px;
    background: #436EF6;
    border-radius: 4px;
    outline: none;
    border: none;
    color: #fff;
}
#data-content .myHeader-data .headerBtn:hover {
      background: #1248f4;
}
#data-content .myHeader-data .headerBtn:active {
      background: #3966f6;
}
#data-content .myHeader-data .valBlock {
    position: absolute;
    top: 55px;
    right: 20px;
    width: 210px;
    background: #fff;
    border: 1px solid #597dd2;
    box-shadow: 0px 13px 12px 1px rgba(215, 231, 254, 0.5);
    border-radius: 8px;
    z-index: 99;
}
#data-content .myHeader-data .valBlock .listBlock {
      max-height: 480px;
      overflow: auto;
      border-top-right-radius: 8px;
      border-top-left-radius: 8px;
}
#data-content .myHeader-data .valBlock .listBlock .valList {
        display: flex;
        padding: 0 10px;
        height: 40px;
        line-height: 40px;
        justify-content: space-between;
        cursor: pointer;
}
#data-content .myHeader-data .valBlock .listBlock .valList:nth-of-type(odd) {
          background: #EFF1F6;
}
#data-content .myHeader-data .valBlock .listBlock .valList .checkboxClass {
          margin-top: 15px;
}
#data-content .myHeader-data .valBlock .btn-block {
      height: 50px;
      line-height: 50px;
      text-align: center;
}
#data-content .myHeader-data .valBlock .btn-block .btn {
        width: 70px;
        height: 30px;
        border-radius: 4px;
        border: none;
        color: #fff;
        background: #999999;
        margin: 0 5px;
}
#data-content .myHeader-data .valBlock .btn-block .btn:hover {
          background: gray;
}
#data-content .myHeader-data .valBlock .btn-block .btn:active {
          background: #949494;
}
#data-content .myHeader-data .valBlock .btn-block .btn.btnBlue {
          background: #436EF6;
}
#data-content .myHeader-data .valBlock .btn-block .btn.btnBlue:hover {
            background: #1248f4;
}
#data-content .myHeader-data .valBlock .btn-block .btn.btnBlue:active {
            background: #3966f6;
}
#data-content .content {
  background: #e4e8f4;
  padding: 20px;
  flex: 1;
  position: relative;
}
#data-content .content .table_block {
    width: calc(100vw - 200px);
    overflow: auto;
}
#data-content .content .table_block .index_table {
      border-spacing: 0;
      width: calc(100vw - 200px);
}
#data-content .content .table_block .index_table th, #data-content .content .table_block .index_table td {
      height: 44px;
      text-align: center;
      padding: 0 !important;
      width: 130px;
      vertical-align: middle !important;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
}
#data-content .content .table_block .index_table th:nth-child(2), #data-content .content .table_block .index_table td:nth-child(2) {
        width: 150px;
}
#data-content .content .table_block .index_table td {
      font-size: 14px;
      height: 40px;
      padding: 0;
}
#data-content .content .table_block .index_table td .status_circle {
        display: inline-block;
        margin-right: 5px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: #ccc;
        vertical-align: middle;
}
#data-content .content .table_block .index_table td .status-5 {
        background: #ccc;
}
#data-content .content .table_block .index_table td .status-0 {
        background: green;
}
#data-content .content .table_block .index_table td .status-3 {
        background: #f80000;
}
#data-content .content .table_block .index_table td .status-2 {
        background: #ffa101;
}
#data-content .content .table_block .index_table td .status-1 {
        background: #c0b000;
}
#data-content .content .table_block .index_table > thead > tr, #data-content .content .table_block .index_table > tbody > tr {
      padding: 8px 0;
      display: table;
      width: 100%;
      table-layout: fixed;
}
#data-content .content .table_block .index_table > thead > tr {
      border-top-right-radius: 8px;
      border-top-left-radius: 8px;
}
#data-content .content .table_block .index_table > thead > tr > th {
        font-size: 16px;
}
#data-content .content .table_block .index_table > tbody > tr {
      height: 40px;
      padding: 0;
}
#data-content .content .table_block .index_table > thead {
      display: block;
}
#data-content .content .table_block .index_table > tbody {
      display: block;
      height: calc(100vh - 180px);
      font-size: 14px;
      /*height: 800px;*/
      -ms-scroll-chaining: chained;
      -ms-overflow-style: none;
      -ms-content-zooming: zoom;
      -ms-scroll-rails: none;
      -ms-content-zoom-limit-min: 100%;
      -ms-content-zoom-limit-max: 500%;
      -ms-scroll-snap-type: proximity;
      -ms-scroll-snap-points-x: snapList(100%, 200%, 300%, 400%, 500%);
      -ms-overflow-style: none;
      overflow-y: overlay;
}
#data-content .content .table_block .index_table > tbody > tr:nth-child(odd) {
      background-color: #EFF1F6;
}
#data-content .content .table_block .index_table > tbody > tr:nth-child(even) {
      background-color: #fff;
}
#data-content .content .table_block .index_table > tbody > tr:hover {
      background-color: #abb7c7;
}
#data-content .content .table_block .index_table > thead > tr {
      background-color: #C9DBEF;
}
#data-content .content .noData {
    width: 100%;
    height: 50vh;
    position: absolute;
    top: 50%;
    left: 0;
    text-align: center;
    font-size: 20px;
}
